import icons from 'url:../../img/icons.svg';

export default class View {
  _parentElement = document.querySelector('.recipe');
  _data;
  _errorMessage = 'OH! something went wrong!';
  _message = 'success';

  render(data) {
    if (!data || (Array.isArray(data) && data.length === 0))
      return this.errorRender();

    this._data = data;
    const markup = this._generateMarkup();
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  updata(data) {
    this._data = data;
    const newMarkup = this._generateMarkup();
    const newDom = document.createRange().createContextualFragment(newMarkup);
    const newEl = Array.from(newDom.querySelectorAll('*'));
    const curEl = Array.from(this._parentElement.querySelectorAll('*'));

    newEl.forEach((nEl, i) => {
      const cEl = curEl[i];
      if (!nEl.isEqualNode(cEl) && nEl.firstChild?.nodeValue.trim() !== '') {
        cEl.textContent = nEl.textContent;
      }
      if (!nEl.isEqualNode(cEl)) {
        Array.from(nEl.attributes).forEach(attr =>
          cEl.setAttribute(attr.name, attr.value)
        );
      }
    });
  }

  _clear() {
    this._parentElement.innerHTML = '';
  }

  renderSpinner() {
    const markup = `
          <div class="spinner">
          <svg>
            <use href="${icons}#icon-loader"></use>
          </svg>
        </div>
          `;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  errorRender(errMessage = this._errorMessage) {
    const markup = `
      <div class="error">
      <div>
        <svg>
          <use href="${icons}#icon-alert-triangle"></use>
        </svg>
      </div>
      <p>${errMessage}</p>
    </div>
      `;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }

  messageRender(message = this._message) {
    const markup = `
      <div class="message">
      <div>
        <svg>
          <use href="${icons}#icon-smile"></use>
        </svg>
      </div>
      <p>${message}</p>
    </div>
      `;
    this._clear();
    this._parentElement.insertAdjacentHTML('afterbegin', markup);
  }
}
